// –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –æ—Ç—Ä–∏–º–∞–Ω–Ω—è —Å—Ç–∞—Ç–µ–π –∑ New York Times –∑–∞ –∑–∞–¥–∞–Ω–∏–º –∑–∞–ø–∏—Ç–æ–º —Ç–∞ —Å—Ç–æ—Ä—ñ–Ω–∫–æ—é
async function getSearchArticles(searchQuery, pageNumber) {
    let dateForUrl = '';
    try {
        let date = JSON.parse(localStorage.getItem('date'))
            .replace('/', '')
            .replace('/', '');
        dateForUrl = formatDateForUrl(date);
    } catch (error) {
        // –Ø–∫—â–æ –Ω–µ –≤–¥–∞–ª–æ—Å—è –æ—Ç—Ä–∏–º–∞—Ç–∏ –¥–∞—Ç—É –∑ –ª–æ–∫–∞–ª—å–Ω–æ–≥–æ —Å—Ö–æ–≤–∏—â–∞, —Ç–æ –Ω–µ –¥–æ–¥–∞—î–º–æ —ó—ó –≤ URL
    }

    const articlesFetch = await fetch(
        `${urlBase}/search/v2/articlesearch.json?q=${searchQuery}&${apiKey}&page=${pageNumber}${dateForUrl}`
    );
    const articles = await articlesFetch.json();

    let { response, errors } = articles;

    if (errors) {
        handleErrors(errors);
        return;
    }

    const sumPage = limitResults(response.meta.hits);
    let { docs } = response;

    console.log(docs);
    console.log(sumPage);

    return docs;
}

/* –°–ª—É–∂–±–æ–≤—ñ —Ñ—É–Ω–∫—Ü—ñ—ó */
// –§—É–Ω–∫—Ü—ñ—è –¥–ª—è —Ñ–æ—Ä–º–∞—Ç—É–≤–∞–Ω–Ω—è –¥–∞—Ç–∏ –¥–ª—è –≤–∏–∫–æ—Ä–∏—Å—Ç–∞–Ω–Ω—è –≤ URL
function formatDateForUrl(date) {
    return `&begin_date=${date}&end_date=${date}`;
}

// –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –æ–±—Ä–æ–±–∫–∏ –ø–æ–º–∏–ª–æ–∫
function handleErrors(errors) {
    alert(JSON.parse(errors));
}

// –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –æ–±–º–µ–∂–µ–Ω–Ω—è –∫—ñ–ª—å–∫–æ—Å—Ç—ñ —Ä–µ–∑—É–ª—å—Ç–∞—Ç—ñ–≤
function limitResults(hits) {
    return Math.min(hits, 1000);
}

